@use '../abstracts' as *;

html.light {
  --color-text: #{$gray-1};
  --color-text-pre-hover: #{$gray-4};
  --color-background: #{$white};
  --color-surface: #{$color-surface};
  --color-primary: #{$primary-light};
  --color-button: #{$color-button-light};
  --color-on-button: #{$color-on-button-light};
  --color-in-input: #{$color-in-input-light};
}

html.dark {
  --color-text: #{$white};
  --color-text-pre-hover: #{$white-d};
  --color-background: #{$gray-1};
  --color-surface: #{$gray-5};
  --color-primary: #{$primary-dark};
  --color-button: #{$color-button-dark};
  --color-on-button: #{$color-on-button-dark};
  --color-in-input: #{$color-in-input-dark};
}

html, body {
  height: 100%;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  box-sizing: border-box;
  line-height: 1.5;
  background-color: var(--color-background);
  color: var(--color-text);
  overflow-x: hidden;
  font-family: 'Roboto', Arial, sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
figure {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
  overflow-wrap: break-word;
}

ul,
ol,
dd {
  padding: 0;
  margin: 0;
  list-style: none;
}

audio,
canvas,
iframe,
picture,
img,
svg,
video {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  padding: 0;
  margin: 0;
  border: none;
  outline: none;
  font: inherit;
}

select {
  width: 100%;
  background-color: $transparent;
  line-height: inherit;
  cursor: inherit;
  &::-ms-expand {
    display: none;
  }
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}

button {
  background-color: $transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

// input,
// select,
button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

:focus:not(:focus-visible){
  outline: 0;
}
